






























































































.container {
  width: 100vw;
  min-height: 100vh;
  background: #f5f5f5;
  color: #666;
  overflow: auto;
  .section {
    margin: 20px 0;
    background: #fff;
    border: 1px solid #eee;
    .amount-box {
      height: 102px;
      line-height: 102px;
      padding: 0 30px 0 0;
    }
    .box {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 30px 30px 30px 0;
      margin-left: 30px;
      border-bottom: 1px solid #f0f0f0;
      font-size: 30px;
    }
  }
}

.snack-box {
  .snack {
    width: 70%;
  }
}

.stepper {
  display: flex;
  border: 1px solid #ddd8ce;
  height: 60px;
  .num {
    border-right: 1px solid #ddd8ce;
    border-left: 1px solid #ddd8ce;
    font-size: 30px;
    padding: 0 10px;
  }
  div {
    width: 60px;
    height: 60px;
    text-align: center;
    line-height: 60px;
    font-size: 50px;
  }
}

.phone {
  padding: 30px;
  font-size: 30px;
}

.payment-btn {
  height: 2.5em;
  line-height: 2.5em;
  background: #f90;
  margin: 0 30px 20px;
  color: #fff;
  text-align: center;
  font-size: 34px;
  border-radius: 8px;
}

.red {
  color: #f03d37;
}
